import React from 'react';
import './App.css';
import Main from "./components/main";
import { BrowserRouter as Switch, Route, HashRouter } from "react-router-dom";


function App() {
  return (
    <div>
      <HashRouter>
        <Switch>
          <Route path="/testPages" exact component={Main} />
        </Switch>
      </HashRouter>
    </div>
  );
}

export default App;
